@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

.background {
  height: 100vh;
  background: url('./background.jpg');
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
}

.title {
  align-items: center;
  align-content: center;
  margin-bottom: 15px;
  height: 2vh;
}

.title h2 {
  font-family: 'Bebas Neue', cursive;
  font-size: 350%;
  color: #160077;
}

.title button {
  cursor: pointer;
  font-size: 300%;
  font-family: 'Bebas Neue', cursive;
  color: rgb(255, 255, 255);
  border-radius: 20px;
  background: none;
  min-height: 70px;
  margin-top: 90px;
}

.title button:hover {
  transition: all 0.8s ease;
  color: #9ba5ff;
  cursor: pointer;
  font-size: 250%;
  transform-origin: center;
  text-shadow: 0 0 10px rgb(255, 0, 0);
}

.border {
  position: absolute;
  left: 10;
  background: currentColor;
}

.border-top,
.menu .border-bottom {
  width: 100%;
  height: 1px;
  transform: scaleX(0);
}

.border-left,
.border-right {
  width: 1px;
  height: 100%;
  transform: scaleY(0);
}

.border-top,
.border-left,
.border-right {
  top: 0;
}

.border-bottom {
  bottom: 0;
  transform-origin: bottom right;
}

.border-top {
  transform-origin: top left;
}

.border-left {
  transform-origin: bottom left;
}

.border-right {
  left: auto;
  right: 0;
  transform-origin: top right;
}
